.video {
  position: relative;
  width: 100%;
  height: 228px;
  background-color: $text-color;

  @include vp-1199 {
    width: 266px;
    height: 167px;
  }

  @include vp-767 {
    width: 320px;
  }
}

.video__link {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100px;
    height: 100px;
    background-image: url("../img/play.svg");
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 1;

    @include vp-1199 {
      width: 73px;
      height: 73px;
    }
  }

  &:hover,
  &:focus {
    &::before {
      width: 110px;
      height: 110px;

      @include vp-1199 {
        width: 90px;
        height: 90px;
      }
    }
  }
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;

  &:hover,
  &:focus {
    opacity: 0.7;
  }
}
